import removeAccents from "./removeAccents";

// funcin para buscar, contempla que las palabras no estn en orden,
// busca que todas las palabras ingresadas estn dentro del nombre del producto
export default function search(userInput, productsArray) {
  let foundedProducts = [];
  let minInput = userInput.toLowerCase(); // lo transformo a minscula
  let cleanInput = removeAccents(minInput); // le saco acentos o carcteres extraos
  let arrayInput = cleanInput.split(" "); // armo un array con las palabras
  // itero los productos
  productsArray.forEach((product) => {
    let productLower = product.title.toLowerCase(); // tomo el nombre del producto y lo pongo en minscula
    let cleanProduct = removeAccents(productLower); // le saco acentos (no es que yo lo ingrese con acentos, pero supongamos que puede fallar)
    let count = 0; // variable contar para luego chequear si todas las palabras estn en el producto
    //ahora itero las palabras
    arrayInput.forEach((word) => {
      // si la palabra se encuentra en el nombre, entonces cuento
      if (cleanProduct.indexOf(word) > -1) {
        count++;
      }
    });
    // si cont tantas veces como palabras ingresadas, entonces el producto ingresa como buscado
    if (count === arrayInput.length) {
      foundedProducts.push(product);
    }
  });
  if (foundedProducts.length > 0) {
    return foundedProducts;
  } else {
    return null;
  }
}
